/**
 * Removes the highlight from the given state and broadcasts the unhighlight 
 * event
 *
 * @param d The state topojson path that triggered the unhighlight event
 */
Choropleth.prototype.unhighlightState = function(d) {
  this.removeHighlight(d).eventHandler().broadcast({ name: 'mouseoutState', id: d.id }, this);
}

/**
 * Draws a red ring around the highlighted state
 *
 * @param d The path of the state being highlighted
 * @return Choropleth
 */
Choropleth.prototype.drawHighlight = function(d) {
  this.states.filter(function(state) { return state.id == d.id})
    .classed('highlighted', true);
  return this;
}

/**
 * Removes the red ring from the unhighlighted state
 *
 * @param d The path of the state having its highlight removed
 * @return Choropleth
 */
Choropleth.prototype.removeHighlight = function(d) {
  this.states.filter(function(state) { return state.id == d.id; })
    .classed('highlighted', false);
  return this;
}


/**
 * Creates a new EventHanlder object
 *
 * @return EventHandler
 */
function EventHandler() {
  this.listeners = {};
}

/**
 * Adds an event listener for events with the given name. Note that
 * this method does not check for uniqueness. If a listener registers
 * for an event multiple times it will receive multiple notifications
 * when the event occurs.
 *
 * @param eventName   The name of the event being listened for
 * @param listener    The object interested in listening for the event
 * @param callback    The callback for handling the event, which is passed
 *                    to the callback as the only parameter
 *
 * @return EventHandler
 */
EventHandler.prototype.on = function(eventName, listener, callback) {
  var listeners = this.listeners[eventName];
  if (!listeners) {
    listeners = [];
  }
  listeners.push({listener: listener, callback: callback.bind(listener)});
  this.listeners[eventName] = listeners;
  
  return this;
}

/**
 * Informs all interested listeners of the occurrence of a given event. 
 * If the caller provides a value for sender, the sender will not be 
 * informed of the event.
 *
 * @param event   An object containing important event properties. 
 *                'name' is the only required property.
 * @param sender  (Optional) The object that broadcast the event. If 
 *                specified then the sender will not have any of its
 *                callbacks fired.
 *
 * @return EventHandler
 */
EventHandler.prototype.broadcast = function(event, sender) {
  var listeners = this.listeners[event.name];
  if (listeners) {
    listeners.forEach(function(listener) {
      if (listener.listener != sender) {
        listener.callback(event);
      }
    });
  }
  return this;
}

// code found at http://bl.ocks.org/benvandyke/8459843
function Regression() {
  this.xSeries = function(xSeries) {
    if (!xSeries) {
      return this._xSeries;
    }
    
    this._xSeries = xSeries;
    return this;
  };
  
  this.ySeries = function(ySeries) {
    if (!ySeries) {
      return this._ySeries;
    }
    
    this._ySeries = ySeries;
    return this;
  };
  
  this.reduceSum = function(prev, cur) {
    return prev + cur;
  };
  
  this.coefficients = function() {
    var self = this;
    var xBar = this._xSeries.reduce(this.reduceSum) * 1.0 / this._xSeries.length;
    var yBar = this._ySeries.reduce(this.reduceSum) * 1.0 / this._ySeries.length;

    var ssXX = this._xSeries.map(function(d) { return Math.pow(d - xBar, 2); })
      .reduce(this.reduceSum);
    
    var ssYY = this._ySeries.map(function(d) { return Math.pow(d - yBar, 2); })
      .reduce(this.reduceSum);
      
    var ssXY = this._xSeries.map(function(d, i) { return (d - xBar) * (self._ySeries[i] - yBar); })
      .reduce(this.reduceSum);
      
    var slope = ssXY / ssXX;
    var intercept = yBar - (xBar * slope);
    var rSquare = Math.pow(ssXY, 2) / (ssXX * ssYY);
    
    return [slope, intercept, rSquare];
  };
}
/**
 * Creates a new Scatterplot visualization
 *
 * @param parentElem    The ID of the DOM element within which the visualization
 *                      should be drawn.
 * @param handler       The event handler for the visualization
 * @param deaths        An array of death data sorted alphabetically by state
 * @param income        An array of income data sorted alphabetically by state
 * @param unemployment  An array of unemployment data sorted alphabetically by 
 *                      state
 * @return Scatterplot
 */
function Scatterplot(parentElem, factor, deaths, income, unemployment) {
  this.parentElem = parentElem;
  this.data = {
    'death_rate': {
      data: deaths,
      name: 'Deaths Per 100,000',
      domain: [
        d3.min(deaths, function(d) { return d3.min(d.years, function(d) {return d.death_rate; }); }),
        d3.max(deaths, function(d) { return d3.max(d.years, function(d) {return d.death_rate; }); })
      ]
    },
    'median_income': {
      data: income,
      name: 'Median Income',
      domain: [
        d3.min(income, function(d) { return d3.min(d.years, function(d) {return d.median_income; }); }),
        d3.max(income, function(d) { return d3.max(d.years, function(d) {return d.median_income; }); })
      ]
    },
    'unemployment': {
      data: unemployment,
      name: 'Unemployment Rate',
      domain: [
        d3.min(unemployment, function(d) { return d3.min(d.years, function(d) {return d.unemployment; }); }),
        d3.max(unemployment, function(d) { return d3.max(d.years, function(d) {return d.unemployment; }); })
      ]
    }
  };
  
  this.factor = factor;
  this.factorYear = parseYear('2014');
  this.deathRateYear = parseYear('2014');
  
  this.displayData = {};
  this.regression = new Regression();
  
  this.default = {
    duration: 1000
  }
}

/**
 * If a handler is specified the Scatterplot updates its event handler and 
 * registers listeners for the events for which it's interested. The receiving
 * object is returned.
 * If no handler is given returns the objects current handler.
 *
 * @param handler (Optional) The event handler for the object
 * @return EventHandler|Scatterplot
 */
Scatterplot.prototype.eventHandler = function(handler) {
  if (handler) {
    this._eventHandler = handler;
    this._eventHandler.on('update', this, this.handleUpdate);
    this._eventHandler.on('resize', this, this.handleResize);
    this._eventHandler.on('mouseoverState', this, this.drawHighlight);
    this._eventHandler.on('mouseoutState', this, this.removeHighlight);
  
    return this;
  }
  return this._eventHandler;
}

